  getData(){
    var sessionId = localStorage.getItem('sessionId');
    const url = "/api/selectQuery?sessionId="+sessionId+"&startRow=0&maxRows=10&query=select id,name,createdAt from document1&output=json";
    return this.http.get(url).map((response)=> response)
  }

