import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { LoginService } from '../../services/login.service';

@Component({
  selector: 'app-user',
  templateUrl: 'user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  user: any = {};
  toggle = false;
  constructor(private userService: UserService, public loginService: LoginService, public router: Router) {
    console.log('constructor ran');
  }
  ngOnInit() {
    if (localStorage.getItem('currentUser') && localStorage.getItem('currentUser') !== 'undefined') {
      this.getData();
    }else {
      localStorage.setItem('currentUser', 'undefined');
      this.router.navigate(['/']);
    }
  }

    getData() {
      let userId;
      userId = JSON.parse(localStorage.getItem('currentUser')).user.id;
      this.userService.getUser(userId).subscribe(res => {
        console.log(res);
        this.user = res.json().user;
      }, err => {
        console.log(err);
        localStorage.setItem('currentUser', 'undefined');
        this.router.navigate(['/']);
      })
    }

    editClick() {
      this.toggle = true;
    }

    saveEdit(obj) {
      console.log(obj);
      this.toggle = false;
      this.userService.saveEdit(obj).then(res => {
        console.log(res);
        this.getData();
        alert('User updated successfully!');
        return;
      })
    }

    delete(lock) {
    console.log(lock);
    this.userService.deleteUser().then(res => {
      console.log(res);
      this.loginService.getLoggedInName.next({username: undefined});
      localStorage.setItem('currentUser', 'undefined');
      this.router.navigate(['/']);
    })
    }

}





