import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { LoginService } from '../../services/login.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, OnDestroy {
  username: string;
  toggle = false;
  subscription: any;
  currentUser: any = {};
  constructor(public router: Router, public loginService: LoginService) {
    this.subscription = loginService.getLoggedInName.subscribe(user => this.changeName(user));
   }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

   changeName(user: object): void {
    this.currentUser = user;
    if (this.currentUser.username !== undefined) {
      this.username = this.currentUser.username;
      this.toggle = true;
    }else {
      this.toggle = false;
    }
  }

  logout() {
    localStorage.removeItem('currentUser');
    localStorage.clear();
    this.loginService.getLoggedInName.next({username: undefined})
    this.router.navigate(['/login']);
  }

}



