import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import { LockService } from '../../services/lock.service';
import { UserService } from '../../services/user.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { LoginService } from '../../services/login.service';

@Component({
  selector: 'app-lock',
  templateUrl: './lock.component.html',
  styleUrls: ['./lock.component.css']
})
export class LockComponent implements OnInit {
  lock: any = { name: '' };
  locks: any = [];
  toggle = false;
  indexVal;
  users: any = [];
  constructor(private http: Http, public lockService: LockService,
     public loginService: LoginService, public router: Router, public userService: UserService) {

    }

  ngOnInit() {
    if (localStorage.getItem('currentUser') &&
     localStorage.getItem('currentUser') !== 'undefined' &&
      localStorage.getItem('currentUser') !== '') {
        this.loginService.getLoggedInName.next({username: JSON.parse(localStorage.getItem('currentUser')).user.username});
      }
   this.getData();
  }

  submitLock(formData) {
    this.lock.name = formData.value.name;
    this.lockService.addLock(this.lock).then(res => {
      this.getData();
      formData.reset();
      alert('Lock added successfully!');
      return;
    }).catch(err => {
       console.log(err);
       alert(err.error);
       return;
    })
}

getData() {
    this.lockService.getLocks().subscribe(res => {
       console.log(res.json());
       this.locks = res.json().lock;
       this.getUsers();
    }, err => {
       console.log(err);
       localStorage.setItem('currentUser', 'undefined');
       this.router.navigate(['/']);
    })
  }

  editClick(obj, i) {
    this.indexVal = i;
  }

  saveEdit(obj) {
    console.log(obj);
    this.indexVal = undefined;
    this.lockService.editLock(obj.id, obj).then(res => {
      console.log(res);
      this.getData();
      alert('Lock updated successfully!');
      return;
    })
  }

  delete(lock) {
  console.log(lock);
  this.lockService.deleteLock(lock.id).then(res => {
    console.log(res);
    this.getData();
    alert('Lock deleted successfully!');
    return;
  })
  }

  getUsers() {
    this.userService.getUsers().subscribe(res => {
      // console.log('<<<<<<<<<<<<<<>>>>>>>>>>>>>');
      console.log(res.json());
      this.users = res.json().user;
    }, err => {
      console.log(err);
    })
  }
}



