                      this.colSettings = {
                        id:"",
                        name:"",
                        createdAt:""
                      };
                      this.objKeys = Object.keys(this.colSettings);

                      arrObj.forEach(newJobItem => {
                        //console.log(newJobItem);
                        var i =0;
                        var obj = {};
                          this.objKeys.forEach((item)=>{
                            obj[item] = newJobItem[i];
                              i++;
                          })
                          this.newArray.push(obj);
                           console.log(this.newArray);

                      });
                       //console.log(this.newArray);
