import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Headers, RequestOptions, ResponseContentType } from '@angular/http';
// import { SharedService } from '../services/data.service';
// import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
declare var $: any;

@Injectable()
export class UserService {
  headers: Headers;
  options: RequestOptions;

  constructor(public http: Http) {
    console.log('data service connected');
   // this.headers      = new Headers({ 'Content-Type': 'application/json'});
    // this.options       = new RequestOptions({ headers: this.headers });
  //   if (localStorage.getItem('currentUser') &&
  //   localStorage.getItem('currentUser') !== '' &&
  //    localStorage.getItem('currentUser') !== 'undefined') {
  //    let token;
  //    token = JSON.parse(localStorage.getItem('currentUser')).token;
  //   // console.log(token);
  //    console.log(JSON.parse(localStorage.getItem('currentUser')));
  //    this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
  //    this.options       = new RequestOptions({ headers: this.headers });
  //  }
  }

  getUser(userId) {
      if (localStorage.getItem('currentUser') &&
        localStorage.getItem('currentUser') !== '' &&
        localStorage.getItem('currentUser') !== 'undefined') {
        let token;
        token = JSON.parse(localStorage.getItem('currentUser')).token;
        // console.log(token);
        console.log(JSON.parse(localStorage.getItem('currentUser')));
        this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
        this.options       = new RequestOptions({ headers: this.headers });
   }
    return this.http.get('/api/user/' + userId, this.options).map((response) => response);
  }

  getUsers() {
    if (localStorage.getItem('currentUser') &&
    localStorage.getItem('currentUser') !== '' &&
    localStorage.getItem('currentUser') !== 'undefined') {
    let token;
    token = JSON.parse(localStorage.getItem('currentUser')).token;
    // console.log(token);
    console.log(JSON.parse(localStorage.getItem('currentUser')));
    this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
    this.options       = new RequestOptions({ headers: this.headers });
}
    return this.http.get('/api/user', this.options).map((response) => response);
  }

  saveEdit(body) {
    if (localStorage.getItem('currentUser') &&
    localStorage.getItem('currentUser') !== '' &&
    localStorage.getItem('currentUser') !== 'undefined') {
    let token;
    token = JSON.parse(localStorage.getItem('currentUser')).token;
    // console.log(token);
    console.log(JSON.parse(localStorage.getItem('currentUser')));
    this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
    this.options       = new RequestOptions({ headers: this.headers });
}
    return this.http.put('/api/user', body, this.options).toPromise()
          .then(this.extractData)
          .catch(this.handleErrorPromise);
  }

  postUser(body) {
    if (localStorage.getItem('currentUser') &&
    localStorage.getItem('currentUser') !== '' &&
    localStorage.getItem('currentUser') !== 'undefined') {
    let token;
    token = JSON.parse(localStorage.getItem('currentUser')).token;
    // console.log(token);
    console.log(JSON.parse(localStorage.getItem('currentUser')));
    this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
    this.options       = new RequestOptions({ headers: this.headers });
}
      return this.http.post('/api/user', body, this.options).toPromise()
      .then((res) => res)
      .catch(this.handleErrorPromise);
  }

  deleteUser() {
    if (localStorage.getItem('currentUser') &&
    localStorage.getItem('currentUser') !== '' &&
    localStorage.getItem('currentUser') !== 'undefined') {
    let token;
    token = JSON.parse(localStorage.getItem('currentUser')).token;
    // console.log(token);
    console.log(JSON.parse(localStorage.getItem('currentUser')));
    this.headers      = new Headers({ 'Content-Type': 'application/json', 'Authorization': token});
    this.options       = new RequestOptions({ headers: this.headers });
}
      return this.http.delete('/api/user', this.options).toPromise()
        .then(this.extractData)
        .catch(this.handleErrorPromise);
  }

   private extractData(res: Response) {
     console.log(res.json());
        const body = res;
        return body || {};
       }

  // // below function can be used when deal with Observable
    private handleErrorPromise (error: Response | any) {
      // localStorage.setItem('currentUser', 'undefined');
      console.log(error.error || error.error);
       // console.error(error.message || error);
       return Promise.reject(error.error || error.error);
    }


}



