import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { UserService } from '../../services/user.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  public email: string;
  public password: string;
  public user = {name: '', username: '', birthdate: '', password: ''};
  constructor(public userService: UserService, public router: Router) { }

  ngOnInit() {
  }

submitForm(formData) {
//  console.log(user);
  // console.log(user.email);
  this.user.name = formData.value.name;
  this.user.username = formData.value.username;
  this.user.birthdate = formData.value.birthdate;
  this.user.password = formData.value.password;
  this.userService.postUser(this.user).then(res => {
    console.log(res);
    alert('Registered successfully,please login');
    this.router.navigate(['/']);
  }).catch(err => {
    console.log(err);
  })
  }
}



